}

header.ant-layout-header .logo {
    width: 120px;
    line-height: normal;
    line-height: initial;
    color: #ffffff;
    font-size: 23px;
    text-align: center;
}

.ant-layout-header {
    background-color: rgb(25, 44, 56)!important;
}
.header header.ant-layout-header {
    justify-content: space-between;
    display: flex;
    align-items: center;
    box-shadow: rgba(10, 28, 39, 0.4) 0px 5px 10px;
    z-index: 1;
    position: relative;
}

.ant-menu.ant-menu-dark .ant-menu-item-selected, .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
    background-color: #0a1c27!important;
}
.ant-menu-dark, .ant-menu-dark .ant-menu-sub {
    background: #192b38!important;
}

._grid img[alt="main_bg"] {
    width: 100%;
    height: auto;
}

._grid .grid_wrapper {
    position: relative;
    will-change: transform;
    -webkit-transition: all 300ms ease 0s;
    transition: all 300ms ease 0s;
    cursor: pointer;
}

._grid .grid_wrapper img[alt="small_bg"] {
    z-index: 1;
    bottom: 0px;
    left: 38%;
    width: 68%;
    position: absolute;
    will-change: transform;
    -webkit-transition: all 300ms ease 0s;
    transition: all 300ms ease 0s;
}

._grid .grid_wrapper:hover img[alt="small_bg"] {
    -webkit-transform: translate(3%, 0px);
            transform: translate(3%, 0px);
}

._grid .grid_wrapper:hover {
    -webkit-transform: translate(0px, -4%);
            transform: translate(0px, -4%);
}


._grid .grid_footer {
    margin-top: 1rem;
    display: flex;
    justify-content: space-between;
    color: #ffffff;
    align-items: center;
}

._grid .grid_footer h2 {
    margin-bottom: 0;
    color: #ffffff;
    font-size: 1.5em;
    line-height: normal;
    line-height: initial;
}

._grid .grid_footer span {
    font-size: 14px;
    color: #dfdfdf;
}

._grid#plinko .grid_wrapper img[alt="small_bg"] {
    width: auto;
}

main.ant-layout-content .home {
    padding: 50px 0;
}
main.ant-layout-content .plinko {
    padding-top: 50px;
    padding-bottom: 50px;
    color:#ffffff;
}
.plinko_tab {
    padding: 1rem!important;
    background-color: #263742;
    border-radius: 6px 0px 0px 6px;
}

.plinko> .ant-row {
    box-shadow: rgba(25, 25, 25, 0.75) 0px 0px 10px;
    background-color: rgb(18, 34, 46);
    border-radius: 6px;
    display: flex;
}


.plinko_tab .ant-tabs-bar {border-bottom: navajowhite;}


.plinko_tab .ant-tabs-bar .ant-tabs-nav-scroll .ant-tabs-nav {
    width: 100%;
}

.plinko_tab .ant-tabs-bar .ant-tabs-nav-scroll .ant-tabs-nav div.ant-tabs-ink-bar {display: none!important;}

.plinko_tab.ant-tabs .ant-tabs-small-bar .ant-tabs-tab {
    font-weight: 600;
    font-size: 0.8em;
    touch-action: manipulation;
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    line-height: 1.5;
    color: rgb(183, 191, 214);
    -webkit-transition: background 300ms ease 0s, opacity 300ms ease 0s, -webkit-transform 100ms ease 0s;
    transition: background 300ms ease 0s, opacity 300ms ease 0s, -webkit-transform 100ms ease 0s;
    transition: background 300ms ease 0s, opacity 300ms ease 0s, transform 100ms ease 0s;
    transition: background 300ms ease 0s, opacity 300ms ease 0s, transform 100ms ease 0s, -webkit-transform 100ms ease 0s;
    border-radius: 5px;
    background: rgb(46, 68, 82);
    padding: calc(0.5em + 2px) 1em;
    margin-right: 0px;
    width: 50%;
}

.plinko_tab.ant-tabs .ant-tabs-top-bar .ant-tabs-tab.ant-tabs-tab-active {
    background-color: rgb(21, 33, 45);
}
.plinko_tab.ant-tabs .ant-tabs-top-bar .ant-tabs-tab.ant-tabs-tab-active:hover {
    background-color: rgb(21, 33, 45);
}

.plinko_tab.ant-tabs .ant-tabs-nav div .ant-tabs-tab:first-child {
    border-radius: 6px 0px 0px 6px;
}

.plinko_tab.ant-tabs .ant-tabs-nav div .ant-tabs-tab:last-child {
    border-radius: 0px 6px 6px 0px;
}

.plinko_tab.ant-tabs .ant-tabs-top-bar .ant-tabs-tab:hover {
    background-color: rgb(55, 82, 98);
}

.manual_tab label {
    display: inline-flex;
